/* * CUSTOM STYLES
 */
html {
  background: white;
}

.dei-checker-header {
	position: fixed;
	right: 0;
	top: 0;
}

.dei-checker {
	float: right;
}

.dei-checker img {
	width: 20px;
	height: 20px;
}

.opacity-0 {
  opacity: 0;
}


/*  COLORS --------------------- */
.bg-header { background-color: #001f3f; }
.bg-white { background-color: #fff; }
.text-white, .text-white a { color: #ffffff; }


/*  MISC --------------------- */
.width-tabs { width: 70%; }
.width-aside { width: 30%; }
.width-main { width: 80%; }

.center-horz { margin-left: auto; margin-right: auto; }
.mouse-cursor { cursor: pointer; }

.brt-s { border-top-left-radius: .25rem; border-top-right-radius: .25rem; }
.brb-s { border-bottom-left-radius: .25rem; border-bottom-right-radius: .25rem; }

.slds-tabs_default__link:focus, .slds-tabs_default__link:hover {
  text-decoration: none !important;
}

.tab-focus:focus,
.tab-focus:focus {
  border-bottom: 0.125rem solid #007add; 
  -webkit-transition:height 300ms;
  transition:height 300ms;
}

/*  ============================================================================= */
/*  ============================================================================= */
/*
 * UTILITY STYLES
 */
/*  TYPOGRAPHY --------------------- */
.caps { text-transform: uppercase; }
.italic { font-style:italic; }

.fw-light { font-weight: 200; }
.fw-norm { font-weight: 400; }
.fw-bold { font-weight: 800; }

.tac { text-align: center; }
.tal { text-align: left; }
.tar { text-align: right; }

.text-plain { text-decoration: none; }
.list-plain { padding: 0; margin: 0; list-style-type: none; }
.list-horz li { display: inline-block; }

/*  LAYOUT & DISPLAY --------------------- */
.dn { display: none; }
.db { display: block; }
.dib { display: inline-block; }
.dt { display: table; }
.dtc { display: table-cell; }

.df { display: flex; }
.df-center { align-items: center; }
.df-justify { justify-content: space-between; }
.df-around { justify-content: space-around; }
.df-start { justify-content: flex-start; }

.pos-rel { position: relative; }
.pos-abs { position: absolute; }
.pos-fix { position: fixed; }
.top-0 { top: 0; }
.bot-0 { bottom: 0; }
.left-0 { left: 0; }
.right-0 { right: 0; }

.fl { float: left; }
.fr { float: right; }
.cf:after {
  content: "";
  display: table;
  clear: both;
}

.center { margin-left: auto; margin-right: auto; }
.a-top { vertical-align: top; }
.a-mid { vertical-align: middle; }
.a-bot { vertical-align: bottom; }
.wi-full { width: 100%; }
.ht-full { height: 100%; }

.z-above { z-index: 10; }
.z-top { z-index: 999; }


/* SHAPES & FORM DEFAULTS ---------------------  */
.circle { border-radius: 100%; }
.br-s { border-radius: 6px; }


/* BORDER WIDTHS & STYLE ---------------------*/
.ban { border: none !important; }
.bas { border-width: 1px; border-style: solid; }
.bls { border-left-width: 1px; border-left-style: solid; }
.brs { border-right-width: 1px; border-right-style: solid; }
.bts { border-top-width: 1px; border-top-style: solid; }
.bbs { border-bottom-width: 1px; border-bottom-style: solid; }
.border-gray { border-color: #dddbda; }

